.AutocompleteTextBox {
	width:100%;
	border: 1px solid #808080;
	box-shadow: 0 0 0 1px rgba(0,0,0,.1), 0 2px 4px 1px rgba(0,0,0,.18);
	font-family:Arial, Helvetica, sans-serif;
	font-size:14px;
	color:rgba(0,0,0,.73);
}

.AutocompleteTextBox input {
	width:100%;
	border:none;
	font-family:Arial, Helvetica, sans-serif;
	font-size:14px;
	color:rgba(0,0,0,.73);
	padding: 10px 5px;
	box-sizing: border-box;
	outline:none;
}

.AutocompleteTextBox ul {
	list-style-type: none;
	text-align: left;
	margin: 0;
	padding: 0;
	border-top:1px solid #808080;
}

.AutocompleteTextBox ul::before {
	content: "";
}

.AutocompleteTextBox li {
	padding: 10px 5px;
	cursor:pointer;
}

.AutocompleteTextBox li:hover {
	text-decoration: underline;
	background-color: rgba(128,128,128,.20);
}